import { TemplateRef, ContentChild, Component, Input, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';

@Component({
  selector: 'app-suggest',
  template: require('./suggest.component.html'),
  styles: [require('./suggest.component.css')]
})
export class SuggestComponent {
    @ContentChild(TemplateRef) template:TemplateRef<any>;

  @Input() placeholder: string = '';
  @Input() delay: number = 300;


  @Output() complete: EventEmitter<string> = new EventEmitter();
  @Output('select') selected: EventEmitter<any> = new EventEmitter();
  inputSubject:Subject<string> = new Subject();
  
  public inputValue: string = "";
  public suggestData:Array<any> = [];
  constructor() {
        this.inputSubject
      .debounceTime(this.delay)
      .distinctUntilChanged()
      .subscribe(input => { this.complete.emit(input) });
   }

  @Input() set suggest(suggestData:Array<any>){
    this.suggestData = suggestData;
  }

  updateSuggest(evt:Event){
    this.inputSubject.next(this.inputValue);
  }

  public select(item:any){
    this.selected.emit(item);
    this.suggestData = [];
  }

}



