import { Component, ViewEncapsulation } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/observable/from';
import 'rxjs/add/observable/interval';
import 'rxjs/add/observable/onErrorResumeNext';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/zip';
import 'rxjs/add/operator/take';
import * as papaparse from 'papaparse';

declare function escape(s: string): string;

interface Rcp {
  title: string;
  id: string;
  content: string;
  title_search: string;
}

@Component({
  selector: 'toolbar-app',
  template: require('./toolbar-app.html'),
  encapsulation: ViewEncapsulation.None,
  styles: [require('./toolbar-app.css')]
})
export class ToolbarAppComponent {
  result: Array<Rcp> = [];
  rcp: Rcp;
  title: string = 'app works!';

  private db;

  constructor(private http: Http) {
    let openDatabaseRequest = indexedDB.open("rcp");
    openDatabaseRequest.onsuccess = () => {
      this.db = openDatabaseRequest.result;
      let rcpStore = this.db.transaction(["monographies"], "readonly").objectStore('monographies');
      let count = rcpStore.count();
      count.onsuccess = () => console.log(count.result);
    }
    openDatabaseRequest.onupgradeneeded = () => {
      let store = openDatabaseRequest.result.createObjectStore("monographies", { keyPath: 'id' });
      store.createIndex('by_title', "title_search", { unique: false });
    }
  }

  addRcp(rcp: Rcp, callback?: () => void) {
    let transaction = this.db.transaction('monographies', 'readwrite');
    let rcpStore = transaction.objectStore('monographies');
    let request = rcpStore.put(rcp);
    transaction.oncomplete = () => callback();
    transaction.onerror = () => {
      console.error("error : " + transaction.error);
    }
  }

  getRcp(id: string, callback: (result: Rcp) => void) {
    let rcpStore = this.db.transaction(["monographies"], "readonly").objectStore('monographies');
    let query = rcpStore.get(id);
    query.onsuccess = () => {
      let rcp = query.result;
      callback(rcp);
    }
    query.onerror = () => {
      console.error("Read product error : " + query.error);
    }
  }

  strip(html: string): string {
    let tmp = document.createElement("DIV");
    tmp.innerHTML = html.replace(/<(\/?img)/g, "<$1_none");
    return tmp.textContent || tmp.innerText || "";
  }

  public search(text: string) {
    this.result = [];
    if (!text)
      return;
    let range = IDBKeyRange.bound(text.toLowerCase(), text.toLowerCase() + '\uffff');

    let rcpStore = this.db.transaction(["monographies"], "readonly").objectStore('monographies');
    let request = rcpStore.index("by_title").openCursor(range);
    request.onsuccess = (r) => {
      let cursor = request.result
      if (cursor) {
        if (cursor.value)
          this.result.push(cursor.value);

        if (this.result.length < 30)
          cursor.continue();
        else
          cursor.advance(100000);
      }
    }
  }

  public selectRcp(rcp: Rcp) {
    this.rcp = rcp;
  }

  public reload() {
    let i = 0;
    let tick = new Subject();

    this.http
      .get('https://crossorigin.me/http://base-donnees-publique.medicaments.gouv.fr/telechargement.php?fichier=CIS_bdpm.txt')
      .map(res => res.text())
      .map(txt => papaparse.parse(txt, { delimiter: "\t" }).data)
      .mergeMap(tbl => Observable.from(tbl))
      .map(row => row[0])
      .mergeMap(cis => {
        let s: Subject<string> = new Subject();
        this.getRcp(cis, (mono) => {
          if (!mono) {
            s.next(cis);
          }
          else {
            console.log("here:", cis);
            s.complete();
          }
        });
        return s;
      })
      // .take(20)
      .zip(tick)
      .map(it => it[0])
      .mergeMap(cis => {
        let url = 'https://crossorigin.me/http://m.base-donnees-publique.medicaments.gouv.fr/rcp-' + cis + '-0?partial=true'
        console.log(url);
        return Observable.onErrorResumeNext(this.http.get(url));
      })
      .map(res => {
        console.log(res);
        return {
          content: res.text(),
          cis: res.url.match(/rcp-(\d+)-/)[1]
        }
      })
      .subscribe((rcp) => {
        console.log('rcp', rcp);
        let match = rcp.content.match(/class="AmmDenomination">(.*?)<\/p>/);
        if (!match) return tick.next();
        let title = match[1].trim();
        let mono = { title: title, id: rcp.cis, content: rcp.content, title_search: title.toLowerCase() };
        this.addRcp(mono, () => { console.log('added', rcp.cis); tick.next() });
        i++;
        console.log(i);
      });
    tick.next();
  }
}



